
.finished {
  text-decoration: line-through;
  color: rgb(204, 204, 204);
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.inputbox {
  width: 250px;
  height: 30px;
  border-radius: 5px;
	font-weight: bold;
  font-size: 20px;
}
.inputbox:focus {
  outline: none;
}
.inputbox::-webkit-input-placeholder{
  font-style: italic;
	font-weight: bold;
	color: #d4d0d0;
  font-size: 20px;
}
ul {
  list-style-type: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
li {
  font-size: 20px;
  position: relative;
  right: 20px;
  margin-bottom: 20px;
}
li:hover .destroy {
  display: inline-block;
}
.destroy {
  position: absolute;
	top: -7px;
  display: none;
	margin: auto 0;
	font-size: 30px;
	color: #cc9a9a;
  background-color: #fff;
  border: none;
  width: 40px;
  height: 40px;
}
.destroy:hover {
	color: #af5b5e;
}
.destroy:after {
	content: '\D7';
}
.destroy:focus {
  outline: none;
}
