.toggle {
  width: 100px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  background-color: rgb(218, 218, 218);
}
.toggle:focus {
  outline: none;
}

